*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 400;
  background-color: #ffffff;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-display: optional;
  src: url("../fonts/opensans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-display: optional;
  src: url("../fonts/opensansbold.woff2") format("woff2");
  font-display: swap;
}
.nav {
  position: relative;
  z-index: 10;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    padding-left: 50px;
    padding-right: 50px;
  }
}
@media (min-width: 1150px) {
  .nav {
    justify-content: space-between;
    max-width: 1050px;
    padding: 0;
    margin: 0 auto;
  }
}

.nav__logo-container {
  display: flex;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  box-shadow: 0 1px 0 0 #ececec;
}
@media (min-width: 768px) {
  .nav__logo-container {
    box-shadow: none;
    margin-right: auto;
  }
}
@media (min-width: 1150px) {
  .nav__logo-container {
    order: 2;
    margin: 0;
    padding-top: 9px;
    padding-bottom: 7px;
  }
}

@media (min-width: 768px) {
  .nav__logo {
    align-self: center;
  }
}
.nav__logo:hover {
  opacity: 0.6;
}
.nav__logo:focus, .nav__logo:active {
  opacity: 0.3;
}

.nav__toggle {
  position: absolute;
  width: 20px;
  height: 14px;
  right: 22px;
  top: 24px;
  padding: 15px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.nav__toggle:hover {
  opacity: 0.6;
}
.nav__toggle:focus, .nav__toggle:active {
  opacity: 0.3;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 700;
}
@media (min-width: 768px) {
  .nav__list {
    display: flex;
  }
}

.nav__item {
  padding-top: 28px;
  padding-bottom: 26px;
  padding-left: 30px;
  padding-right: 30px;
  box-shadow: 0 1px 0 0 #ececec;
  align-self: center;
}
@media (min-width: 768px) {
  .nav__item {
    padding: 0;
    box-shadow: none;
    font-size: 15px;
    line-height: 24px;
  }
}
.nav__item:hover, .nav__item:focus {
  opacity: 0.6;
}
.nav__item:active {
  opacity: 0.3;
}

.site-nav {
  position: absolute;
  background-color: #ffffff;
  width: 100%;
  margin-top: 1px;
}
@media (min-width: 768px) {
  .site-nav {
    display: flex;
    gap: 40px;
    top: 92px;
    left: 50px;
    order: 2;
    margin-top: 0;
    width: auto;
    background-color: transparent;
  }
}
@media (min-width: 1150px) {
  .site-nav {
    position: static;
    order: 1;
  }
}

@media (min-width: 768px) {
  .site-nav__item {
    padding-top: 21px;
  }
}
@media (min-width: 1150px) {
  .site-nav__item {
    font-size: 17px;
    padding: 32px 0;
  }
}

.nav__link {
  text-decoration: none;
  color: #000000;
  padding-left: 46px;
}
@media (min-width: 768px) {
  .nav__link {
    padding: 0;
  }
}

.nav__button {
  font: inherit;
  background-color: transparent;
  border: none;
  padding: 0;
  display: flex;
  gap: 36px;
}

.nav__box-icon {
  opacity: 0.2;
}

.nav__button:hover .nav__box-icon {
  opacity: 1;
  color: #63d1bb;
}

.nav__button:active .nav__box-icon {
  opacity: 0.3;
}

@media (min-width: 768px) {
  .nav__button--search {
    display: flex;
    padding: 36px 32px 34px 34px;
  }
}

@media (min-width: 768px) {
  .nav__button--box {
    padding: 33px 0 33px 33px;
  }
}

.user-nav {
  position: absolute;
  margin-top: 1px;
  top: 237px;
  background-color: #ffffff;
  width: 100%;
}
@media (min-width: 768px) {
  .user-nav {
    position: static;
    width: auto;
    margin-top: 0;
    margin-left: auto;
  }
}
@media (min-width: 1150px) {
  .user-nav {
    order: 3;
    margin: 0;
  }
}

@media (min-width: 768px) {
  .user-nav__item {
    font-size: 17px;
  }
}

@media (min-width: 768px) {
  .nav__button-text--search {
    display: none;
  }
}

.sale-nav {
  display: none;
}
@media (min-width: 768px) {
  .sale-nav {
    position: absolute;
    display: flex;
    right: 50px;
    top: 92px;
    padding-top: 18px;
  }
}
@media (min-width: 1150px) {
  .sale-nav {
    width: 100%;
    justify-content: space-between;
    top: 95px;
    left: 0;
  }
}

.sale-nav__wrapper {
  display: none;
}
@media (min-width: 1150px) {
  .sale-nav__wrapper {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    gap: 45px;
  }
}

.sale-nav__text-block {
  display: none;
  font-weight: 400;
  color: #444444;
}
@media (min-width: 768px) {
  .sale-nav__text-block {
    display: flex;
  }
}
@media (min-width: 1150px) {
  .sale-nav__text-block {
    margin: 0;
    padding: 0;
    list-style: none;
  }
}

.nav--closed .nav__toggle::before {
  position: absolute;
  display: block;
  content: "";
  width: 20px;
  height: 2px;
  background-color: #000000;
  top: 8px;
  right: 5px;
  box-shadow: 0 4px 0 0 #000000, 0 8px 0 0 #000000;
}

.nav--opened .nav__toggle::after {
  position: absolute;
  display: block;
  content: "";
  width: 20px;
  height: 2px;
  top: 12px;
  right: 0;
  background-color: #000000;
  transform: rotate(-45deg);
}

.nav--opened .nav__toggle::before {
  position: absolute;
  display: block;
  content: "";
  width: 20px;
  height: 2px;
  top: 12px;
  right: 0;
  background-color: #000000;
  transform: rotate(45deg);
}

@media (max-width: 767px) {
  .nav--closed .site-nav,
.nav--closed .user-nav {
    display: none;
  }
}

@media (max-width: 767px) {
  .nav--opened .site-nav,
.nav--opened .user-nav {
    display: block;
  }
}

@media (max-width: 767px) {
  .nav--nojs .site-nav,
.nav--nojs .user-nav {
    position: static;
  }
}

@media (max-width: 767px) {
  .nav--nojs .nav__toggle {
    display: none;
  }
}

.toyshop {
